/* ----------PAGE---------- */
.root_page {
  max-width: 640px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.page_number {
  text-align: center;
}
/* ------------------------ */

/* ---------MODAL---------- */
.root_modal {
  width: 640px;
  margin-left: 2vw;
  margin-right: 2vw;
}
/* ------------------------ */

.cyan_color {
  color: #00cccc;
}

.ingredients_obj {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  max-height: 328px;
  overflow-y: auto;
}

.ingredient_info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.ingredient_icon_name {
  display: flex;
  align-items: center;
}

.ingredient_icon {
  height: 64px;
  width: 64px;
  object-fit: cover;
  border-radius: 50%;
  border: double 2px transparent;
  background-image: linear-gradient(black, black),
    linear-gradient(to right, #7723c0, #5644f2);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.ingredient_qty_price {
  display: flex;
  align-items: center;
}

.time_total {
  display: flex;
  justify-content: space-between;
}

.total {
  display: flex;
  align-items: center;
}
